@font-face {
    font-family: "Trajan Pro 3";
    src: url("../assets/fonts/Trajan.ttf");
}

*{
    font-family: 'Trajan Pro 3', serif;
}

html,body{
    margin: 0;
}
.logo{
    display: flex;
    gap: 30px;
    align-items: center;
}

.logo__icon{
    height: 87px;
    width: 87px;

}
.first-banner{
    background:  radial-gradient(107.18% 107.18% at 50% -7.18%, #A4F4FF 0%, #0D4F9F 78.58%), url("../assets/images/banners/firstbanner.png") no-repeat center;
    background-blend-mode: hard-light;
    height: calc(100vh - 70px);
    padding: 35px 200px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    background-size: 100%;
}

header{
    background: rgba(37, 164, 255, 0.2);
    backdrop-filter: blur(2px);
    position: relative;
    top: -35px;
    padding: 60px;
    max-width: 717px;
    height: calc(100% - 190px);
    display: flex;
    justify-content: space-between;
   flex-direction: column;
}

.header-navbar{
    display: flex;
    gap: 30px;

}

.header-navbar a{

    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    color: #0D4F9F;
    text-decoration: none;
}


header h3 {
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 46px;
    color: #FFFFFF;
    max-width: 490px;
}

header button{
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    padding: 30px;
    color: #FFFFFF;
    background: rgba(37, 164, 255, 0);
    width: 100%;
    border: 1px dotted #FFFFFF;
    cursor: pointer;
}

.second-banner {
    position: relative;
    top: -205px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.second-banner img{
    width: 100%;
}



.second-banner__butt {
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-box-shadow: 0px -37px 55px 26px rgba(255, 255, 255, 1);
    -moz-box-shadow: 0px -37px 55px 26px rgba(255, 255, 255, 1);
    box-shadow: 0px -37px 55px 26px rgba(255, 255, 255, 1);
    width: 100%;

}

.second-banner__butt h1{
   margin-top: 100px;

}
.main-button{
    border: 1px solid black;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    color: #3C3953;
    padding: 30px;
    background: rgba(0,0,0,0);
    margin: 60px 0;
}

.third-banner div{
    display: flex;
    justify-content: center;
}
.third-banner__text{
    flex-direction: column;
    padding: 100px;

}

.third-banner h3{
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
}

.third-banner {
    font-style: normal;
    font-weight: 200;
    font-size: 20px;
    line-height: 30px;
}

.third-banner__about{
    background: #0D4F9F;
}

.third-banner__about__image,
.third-banner__ref__image
{
    width: 50vw;


}


.third-banner__about__image{
    background: url("../assets/images/banners/banner4.png") no-repeat;
    background-size: cover;
}

.third-banner__ref__image{
    background:  radial-gradient(107.18% 107.18% at 50% -7.18%, #A4F4FF 0%, #0D4F9F 78.58%), url("../assets/images/banners/banner4ref.png") no-repeat center;
    background-blend-mode: hard-light;
    background-size: cover;

}



.third-banner__ref__text,
.third-banner__about__text{
    width: calc(50vw - 200px);

}


.third-banner__about__text h3,
.third-banner__about__text p
{
    color: white;
}

.third-banner__about__text,
.third-banner__ref__text
{
    flex-direction: column;
    padding: 100px;
}

.third-banner__ref__text button
{
    max-width: 450px;
    margin: 60px auto;
}



footer{
    background: #0D4F9F;
    padding: 100px 200px;
    display: flex;
    justify-content: space-between;
    color: white;

}


.myfooter__socials {
    display: block !important;
}

.myfooter__socials h1{
    font-style: normal;
    font-weight: 200;
    font-size: 36px;
    color: #FFFFFF;
}

.myfooter__contacts h3{
    font-style: normal;
    font-weight: 200;
    font-size: 20px;
    color: #FFFFFF;
}

.myfooter__socials__icons{
    display: flex;
    gap: 15px;
}

